import { Card, Col, Row } from "antd";
import { FaMapMarkedAlt } from "react-icons/fa";
import React from "react";

const CBoxTotalPerolehan = ({ data = 0, onClick }) => {
  return (
    <Col span={6}>
      <div onClick={onClick}>
        <Card
          style={{
            backgroundColor: "#dc3545",
            borderRadius: "10px",
            height: 150,
            width: "100%",
          }}
          hoverable={true}
        >
          <Row>
            <Col span={8}>
              <FaMapMarkedAlt style={{ fontSize: "60px", color: "white" }} />
            </Col>
            <Col span={16} style={{ textAlign: "center" }}>
              <div
                style={{
                  color: "white",
                  fontSize: 16,
                  fontWeight: "bold",
                }}
              >
                {data.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, "$&,")}
              </div>
              <h3
                style={{
                  color: "white",
                }}
              >
                Total Harga Perolehan
              </h3>
            </Col>
          </Row>
        </Card>
      </div>
    </Col>
  );
};

export default CBoxTotalPerolehan;
